
















import Vue from 'vue';

export default Vue.extend({
  name: 'todo-filters',
  data() {
    return {
      cardTitle: '', // название карточки
      status: '', // статус карточки
    };
  },
  watch: {
    cardTitle() {
      this.$store.dispatch('setCardTitleAction', this.cardTitle);
    },
    status() {
      this.$store.dispatch('setStatusAction', this.status);
    },
  },
});
