















import Vue from 'vue';

export default Vue.extend({
  name: 'todo-item',
  props: {
    todoItem: Object,
    todoIndex: Number,
    groupId: Number,
  },
  methods: {
    deleteTodo() {
      // удаление задачи
      const payload: object = {
        groupId: this.groupId,
        todoId: this.todoIndex,
      };

      this.$store.dispatch('deleteTodoAction', payload);
    },
  },
});
